import React, { useState } from 'react';
import style from './FAQ.scss';
import SearchInput from './../../../reuse_Components/SearchInput/SearchInput';
import Accordion from './Accordion/Accordion';
import NavLinks from '../../../reuse_Components/NavLinks/NavLinks';
import Title from '../../../reuse_Components/Title/Title';

const FAQ = () => {
  const [active, setActive] = useState(true);

  return (
    <section className='PageBottom FAQ'>
      <NavLinks
        data={[
          { url: 'home', name: '', active: false },
          { url: 'about', name: 'F.A.Q.', active: true },
        ]}
      />

      <Title title='F.A.Q.' />

      <SearchInput />

      <Accordion
        active={active}
        setActive={setActive}
        title={'    H5  ?'}
        subtitle={
          ',      H5  ,  .     H5    95%,      ,       .  H5      .'
        }
      />
      <Accordion
        active={active}
        setActive={setActive}
        title={'  ?'}
        subtitle={
          '. ,          .'
        }
      />
      <Accordion
        active={active}
        setActive={setActive}
        title={'    H5?'}
        subtitle={
          ' H5     . H5   ,         ,      ,       .'
        }
      />
      <Accordion
        active={active}
        setActive={setActive}
        title={'    H5?'}
        subtitle={`     - `}
        href={true}
      />
      <Accordion
        active={active}
        setActive={setActive}
        title={'    H5?'}
        subtitle={
          '       . '
        }
      />
      <Accordion
        active={active}
        setActive={setActive}
        title={' ,     ?'}
        subtitle={
          ' ,            .    -     ,    ,          sale@hi5russia.com,       .'
        }
      />
    </section>
  );
};

export default FAQ;
