import NavLinks from '../../../reuse_Components/NavLinks/NavLinks';
import Title from '../../../reuse_Components/Title/Title';
import './omparison.scss';
import Dropdown from 'react-dropdown';
import Card from './reuse/Card/Card';

// let onSelect = (option) => showBusinessCards(option.value);

const options = [
  { label: '', value: 'brand', className: 'myOptionClassName' },
  {
    label: '',
    value: 'franchising',
    className: 'myOptionClassName',
  },
  {
    label: ' ',
    value: 'retailSale',
    className: 'myOptionClassName',
  },
  {
    label: ' ',
    value: 'wholeSale',
    className: 'myOptionClassName',
  },
];

let characteristicInfo = [
  {
    fortress: '0/',
    nicotine: '',
    volume: '120',
    taste: '',
    cold: '',
    country: '',
    availability: '',
  },
  {
    fortress: '0/',
    nicotine: '',
    volume: '120',
    taste: '',
    cold: '',
    country: '',
    availability: '',
  },
  {
    fortress: '0/',
    nicotine: '',
    volume: '120',
    taste: '',
    cold: '',
    country: '',
    availability: '',
  },
];

const omparison = () => {
  return (
    <section className='PageBottom omparison'>
      <NavLinks
        data={[
          { url: 'home', name: '', active: false },
          { url: 'reviews', name: '', active: true },
        ]}
      />

      <Title title=' ' />

      <section className='omparison-prod'>
        <div className='omparison-prodLeft'>
          <div className='omparison-prodLeftCount'>3 </div>
          <Dropdown
            options={options}
            value={options[0]}
            // onChange={onSelect}
            controlClassName='omparison-dropdownControl'
            menuClassName='omparison-dropdownMenu'
            arrowClosed={<span className='omparison-dropdownClosed' />}
            arrowOpen={<span className='omparison-dropdownOpen' />}
          />
          <div className='omparison-prodLeftCheckbox'>
            <input
              name='notifications'
              component='input'
              type='checkbox'
              id='notifications'
            ></input>
            <label htmlFor='notifications'> </label>
          </div>

          <div className='omparison-prodLeftCircles'>
            <div className='omparison-prodLeftCircle'></div>
            <div className='omparison-prodLeftCircle'></div>
          </div>
        </div>

        <div className='omparison-prodRight'>
          <div className='omparison-prodRightTitle'>
             {' '}
            <span className='omparison-prodRightDelete'>&#10006;</span>
          </div>
          <div className='omparison-prodRightCarousel'>
            <Card
              info={[
                { title: ' ', count: 1990 },
                { title: ' ', count: 1990 },
                { title: ' ', count: 1990 },
              ]}
            />
          </div>
        </div>
      </section>

      <section className='omparison-characteristic'>
        <div className='omparison-characteristicTitle'></div>
        <div className='omparison-characteristicBlock'>
          <div className='omparison-characteristicInfo'>
            <div className='omparison-characteristicSub'></div>
            <div className='omparison-characteristicSub'> </div>
            <div className='omparison-characteristicSub'></div>
            <div className='omparison-characteristicSub'></div>
            <div className='omparison-characteristicSub'> </div>
            <div className='omparison-characteristicSub'></div>
            <div className='omparison-characteristicSub'>
                
            </div>
          </div>
          {characteristicInfo.map((item) => (
            <div className='omparison-characteristicInfo'>
              <div className='omparison-characteristicSub'>
                {item.fortress}
              </div>
              <div className='omparison-characteristicSub'>
                {item.nicotine}
              </div>
              <div className='omparison-characteristicSub'>{item.volume}</div>
              <div className='omparison-characteristicSub'>{item.taste}</div>
              <div className='omparison-characteristicSub'>{item.cold}</div>
              <div className='omparison-characteristicSub'>{item.country}</div>
              <div className='omparison-characteristicSub'>
                {item.availability}
              </div>
            </div>
          ))}
        </div>
      </section>
    </section>
  );
};

export default omparison;
